import { Directive, OnInit, OnChanges, SimpleChanges, Input, Injector, Optional } from '@angular/core';
import { BindingData, BindingList, FrameContext, Change, ChangeType } from '@farris/devkit';
import { UploadFile, FFilePreviewComponent, UploadAndPreviewComponent } from '@farris/extend-file-upload';

/**
 * æ è¡¨æ ¼ç»å®æä»¤
 */
@Directive({
  selector: '[farrisFilePreviewBinding]'
})
class FarrisFilePreviewBindingDirective implements OnInit, OnChanges {

  /**
   * æä»¶idå­æ®µè·¯å¾
   */
  @Input('farrisFileIdKey')
  public fileIdKey: string;

  /**
   * æä»¶nameå­æ®µè·¯å¾
   */
  @Input('farrisFileNameKey')
  public fileNameKey: string;

  /**
   * ç»å®æ°æ®
   */
  private get bindingData(): BindingData {
    return this.frameContext.bindingData;
  }

  /**
   * ç»å®æ°æ®åè¡¨
   */
  private get bindingList(): BindingList {
    return this.bindingData.getList();
  }

  /**
   * æé å½æ°
   */
  constructor(
    @Optional() private previewComponent: FFilePreviewComponent,
    private frameContext: FrameContext,
    @Optional() private uploadAndPreviewComponent: UploadAndPreviewComponent,
    @Optional() private injector: Injector
  ) {
  }

  /**
   * æä»¤åå§å
   */
  ngOnInit() {
    this.bindData();
    this.bindingData.changes.subscribe((change: Change) => {
      if (change.type === ChangeType.Load
        || change.type === ChangeType.Append
        || change.type === ChangeType.Remove
      ) {
        this.bindData();
      }
    });
  }

  /**
   * æä»¤è¾å¥åæ´
   */
  ngOnChanges(changes: SimpleChanges) {
  }


  /**
   * ç»å®æ°æ®
   */
  private bindData() {
    const fileInfos = this.getFileInfos();
    if (this.componentRef) {
      this.componentRef.fileInfos = fileInfos;
    }
  }

  /**
   * è·åéä»¶ä¿¡æ¯åè¡¨
   */
  private getFileInfos(): UploadFile[] {
    const listData = this.bindingList.toJSON();
    const idKey = this.bindingList.primaryKey;
    const fileInfos: UploadFile[] = [];

    listData.forEach((itemData: any) => {
      const id = this.getValueByPath(itemData, idKey);
      const fileId = this.getValueByPath(itemData, this.fileIdKey);
      const fileName = this.getValueByPath(itemData, this.fileNameKey);
      const fileSize = this.getValueByPath(itemData, this.fileSizeKey);
      const fileCreateTime = this.getValueByPath(itemData, this.fileCreateTimeKey);

      const fileInfo: any = {
        id: fileId,
        name: fileName,
        size: fileSize,
        createTime: fileCreateTime,
        originalData: itemData,
        extend: {
          metadataId: fileId
        }
      };
      fileInfos.push(fileInfo);
    });

    return fileInfos;
  }

  /**
   * æ ¹æ®å­æ®µè·¯å¾è·åå¼
   */
  private getValueByPath(data: any, path: string): any {
    const keys = path.split('.');
    let currentValue = data;
    keys.forEach((key: string) => {
      currentValue = currentValue[key];
    });
    return currentValue;
  }
  private getUdtPaths(): string[] {
    const paths = this.fileIdKey.split('.');
    paths.pop();
    return paths;
  }
  private get fileSizeKey() {
    const basePaths = this.getUdtPaths();
    return basePaths.concat(['fileSize']).join('.');
  }
  private get fileCreateTimeKey() {
    const basePaths = this.getUdtPaths();
    return basePaths.concat(['fileCreateTime']).join('.');
  }
  private get componentRef() {
    return this.previewComponent || this.uploadAndPreviewComponent || null;
  }
}

export { FarrisFilePreviewBindingDirective };
